<template id="popup">
  <vs-popup fullscreen :title="title" :active.sync="activeInternal">
    <div :ref="popupRef" class="vs-con-loading__container">
      <vs-table class="mb-base" :data="loanTableData" :noDataText="loanTableNoDataText" @selected="selectRow" search>
        <div slot="header" class="flex flex-wrap-reverse items-center flex-grow justify-between">
          <div class="flex flex-wrap-reverse items-center">
            <vs-button class="p-3 mr-4" @click="loadLoanTableData(popupRef)" type="filled" icon="fa-sync" icon-pack="far" :disabled="loanTableBusy">{{ $t('REFRESH') }}</vs-button>
          </div>

          <div class="flex justify-end mr-4">
            <div class="p-2 border border-solid border-grey-light rounded-full d-theme-dark-bg flex items-center justify-between font-medium">
              <span class="mx-2">{{ loanTablePagingInfo }}</span>
            </div>
          </div>
        </div>

        <template slot="thead">
          <vs-th sort-key="loanNumber">
            {{ this.$t('LOAN_NUMBER') }}
          </vs-th>
          <vs-th sort-key="typeName">
            {{ this.$t('LOAN_TYPE') }}
          </vs-th>
          <vs-th sort-key="startDate">
            {{ this.$t('START_DATE') }}
          </vs-th>
          <vs-th sort-key="currencyAbbreviation">
            {{ this.$t('CURRENCY') }}
          </vs-th>
          <vs-th sort-key="loanAmount">
            {{ this.$t('PRINCIPAL_DISBURSED') }}
          </vs-th>
          <vs-th sort-key="outstandingAmount">
            {{ this.$t('BALANCE_OUTSTANDING') }}
          </vs-th>
        </template>

        <template slot-scope="{data}">
          <vs-tr :key="index" v-for="(tr, index) in data" :data="tr">
            <vs-td :data="data[index].loanNumber">
              {{ data[index].loanNumber }}
            </vs-td>
            <vs-td :data="data[index].typeName">
              {{ data[index].typeName | placeholder('-') }}
            </vs-td>
            <vs-td :data="data[index].startDate">
              {{ data[index].startDate | formatUnixDate }}
            </vs-td>
            <vs-td :data="data[index].currencyAbbreviation">
              {{ data[index].currencyAbbreviation | placeholder('-') }}
            </vs-td>
            <vs-td :data="data[index].loanAmount">
              <span :class="negativeNumberStyle(data[index].loanAmount)">
                {{ data[index].loanAmount | currency('') | placeholder('-') }}
              </span>
            </vs-td>
            <vs-td :data="data[index].outstandingAmount">
              <span :class="positiveNumberStyle(data[index].outstandingAmount)">
                {{ data[index].outstandingAmount | currency('') | placeholder('-') }}
              </span>
            </vs-td>
          </vs-tr>
        </template>
      </vs-table>
    </div>
  </vs-popup>
</template>

<script>
import PopupMixin from '@/mixins/commons/PopupMixin'
import NumberStyleMixin from '@/mixins/commons/NumberStyleMixin'
import LoanTableMixin from '@/mixins/loan/LoanTableMixin'

export default {
  name: 'loan-popup',
  mixins: [PopupMixin, LoanTableMixin, NumberStyleMixin],
  mounted: function () {
    this.loadLoanTableData(this.popupRef)
  },
  beforeDestroy: function () {
    this.$el.remove()
  }
}
</script>
